import { Component, OnInit } from '@angular/core';
import { ImageCroppedEvent } from 'ngx-image-cropper';
import { Observable } from 'rxjs';
import { Crop } from '@ionic-native/crop/ngx';
import { File } from '@ionic-native/file/ngx';
import { ActionSheetController } from '@ionic/angular';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { LocalstorageService } from  '../services/localstorage.service';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { PostfeedService } from '../services/postfeed.service';
import { Postfeed } from '../models/postfeed';
import { formatDate } from '@angular/common';

@Component({
  selector: 'app-picture-modal',
  templateUrl: './picture-modal.component.html',
  styleUrls: ['./picture-modal.component.scss'],
})
export class PictureModalComponent implements OnInit {

  myImage = null;
  croppedImage = null;

  croppedImagepath = "";
  isLoading = false;

  token: any;
  idCompany: any;

  postPhotoSel: FormGroup;

  postFeed = {} as Postfeed;

  constructor(private crop: Crop, private camera: Camera,
    public actionSheetController: ActionSheetController, private file: File,
    private localstorageservice: LocalstorageService, public formBuilder: FormBuilder,
    private postfeedservice:PostfeedService) { }

  ngOnInit() {
    this.geStorateData();
    this.postPhotoSel = new FormGroup({
      postText : new FormControl(),//'', Validators.compose([Validators.required, CompanyValidator.isValid])),
      postValue : new FormControl()
    });
  }

  submitForm(){
    this.postFeed.company = this.idCompany;
    this.postFeed.postText = this.postPhotoSel.get('postText').value;
    this.postFeed.productValue = this.postPhotoSel.get('postValue').value;
    let date = new Date().toUTCString();
    //this.postFeed.postDate = formatDate()  date;

    var re = "data:image/jpeg;base64,"; 
    this.postFeed.media = this.croppedImagepath.replace(re,"");

    this.postfeedservice.addPrePost(this.postFeed, this.token).subscribe((ret)=>{
      console.log(ret);
    });
    //console.log('Pelo menos clicou');
  }

  cropImage(fileUrl) {
    this.crop.crop(fileUrl, { quality: 100 })
      .then(
        newPath => {
          this.showCroppedImage(newPath.split('?')[0])
        },
        error => {
          alert('Error cropping image' + error);
        }
      );
  }

  showCroppedImage(ImagePath) {
    this.isLoading = true;
    var copyPath = ImagePath;
    var splitPath = copyPath.split('/');
    var imageName = splitPath[splitPath.length - 1];
    var filePath = ImagePath.split(imageName)[0];

    this.file.readAsDataURL(filePath, imageName).then(base64 => {
      this.croppedImagepath = base64;
      this.isLoading = false;
    }, error => {
      alert('Error in showing image' + error);
      this.isLoading = false;
    });
  }

  async selectImage() {
    const actionSheet = await this.actionSheetController.create({
      header: "Select Image source",
      buttons: [{
        text: 'Load from Library',
        handler: () => {
          this.pickImage(this.camera.PictureSourceType.PHOTOLIBRARY);
        }
      },
      {
        text: 'Use Camera',
        handler: () => {
          this.pickImage(this.camera.PictureSourceType.CAMERA);
        }
      },
      {
        text: 'Cancel',
        role: 'cancel'
      }
      ]
    });
    await actionSheet.present();
  }

  pickImage(sourceType) {
    const options: CameraOptions = {
      quality: 100,
      sourceType: sourceType,
      destinationType: this.camera.DestinationType.FILE_URI,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE
    }
    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64 (DATA_URL):
      // let base64Image = 'data:image/jpeg;base64,' + imageData;
      this.cropImage(imageData)
    }, (err) => {
      // Handle error
    });
  }
  
  geStorateData(){
    let lDataToken = this.localstorageservice.getValue("token").then((value) =>{
      this.token = value;
    }).catch((err) =>{
      console.log("Error to get token");
    });
    let lDataCompanyId = this.localstorageservice.getValue("compId").then((value) =>{
      this.idCompany = value;
    }).catch((err) =>{
      console.log("Error to get Company Id");
    });
  }
}
